/* montserrat-regular - latin_cyrillic */
@font-face {
  font-family: "MullerRegular";
  font-style: normal;
  font-display: swap;
  src: local("MullerRegular"), local("Muller-Regular"), url("../fonts/MullerRegular.woff2") format("woff2"), url("../fonts/MullerRegular.woff") format("woff"); }

/* montserrat-regular - latin_cyrillic */
@font-face {
  font-family: "MullerMedium";
  font-style: normal;
  font-display: swap;
  src: local("MullerMedium"), local("Muller-Medium"), url("../fonts/MullerMedium.woff2") format("woff2"), url("../fonts/MullerMedium.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

a {
  text-decoration: none; }

a:link {
  color: #000000; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  width: 100%;
  height: auto; }

.hidden {
  display: none; }

body {
  font-family: "MullerRegular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: normal; }

.container {
  max-width: 1880px;
  margin: 0 auto;
  padding: 0 5px; }

@media (max-width: 1023px) {
  .container {
    width: 767px; } }

@media (max-width: 767px) {
  .container {
    width: 320px; } }

