.main__request {
  max-width: 40%;
  padding-top: 48px;
  position: relative; }

.main__request,
.main__video {
  max-height: 940px; }

.main__header {
  font-family: "MullerMedium";
  font-size: 64px;
  line-height: 64px;
  letter-spacing: -0.5px; }

.main__information {
  padding-top: 35%; }

.main__header,
.main__text {
  margin-top: 0; }

.main__header {
  margin-bottom: 27px; }

.main__text {
  font-size: 21px;
  line-height: 30px;
  margin-bottom: 25px;
  max-width: 460px; }

.main__inner {
  display: flex;
  justify-content: space-around;
  padding-top: 2%;
  padding-bottom: 2%;
  height: 100vh; }

.main__video {
  position: relative;
  max-width: 60%; }

.main__videobutton,
.main__videotext {
  position: absolute;
  left: 50%;
  transform: translateX(-50%); }

.main__videobutton {
  min-width: 254px;
  height: 254px;
  background-color: inherit;
  border-radius: 50%;
  top: 30%;
  display: flex;
  cursor: pointer;
  border: 0; }

.main__videosvg:hover,
.main__videosvg:focus,
.main__videotext:hover,
.main__videotext:focus {
  opacity: 0.5; }

.main__videosvg {
  background-color: inherit;
  color: #F9572E; }

.main__link,
.main__videotext {
  font-size: 21px; }

.main__link:hover,
.main__link:focus {
  color: #F9572E; }

.main__videotext {
  top: 95%;
  width: 165px;
  line-height: 25px;
  color: #F9572E; }

.main__link {
  position: absolute;
  top: 40px;
  right: 10%;
  color: #000000;
  line-height: 30px; }

.main__link::before {
  content: "";
  position: absolute;
  left: -40px;
  top: -2px;
  background-repeat: no-repeat;
  width: 24px;
  height: 27px;
  background-image: url(../img/phonesvg.svg); }

.main__imagejpg {
  height: 100%; }

.main__buttons {
  display: flex; }

.main__button {
  padding: 15px 31px 15px;
  background-color: #F9572E;
  border: none;
  border-radius: 30px;
  color: #ffffff;
  cursor: pointer; }

.main__scrollbutton {
  position: absolute;
  bottom: 1%;
  width: 245px;
  display: flex;
  font-size: 16px;
  line-height: 18px;
  padding-left: 45px;
  font-family: "MullerMedium";
  color: #F9572E;
  cursor: pointer; }

.main__scrollbutton:hover,
.main__scrollbutton:focus,
.main__button:hover,
.main__button:focus {
  opacity: 0.5; }

.main__scrollbutton::before {
  content: "";
  position: absolute;
  left: 0px;
  top: 0;
  background-repeat: no-repeat;
  width: 41px;
  height: 40px;
  background-image: url(../img/arrowsdown.svg); }

.main__start {
  margin-bottom: 0;
  margin-left: 14px;
  text-align: center; }

@media (width: 1023px) {
  .main__inner {
    flex-direction: column; }
  .main__request,
  .main__video {
    max-width: 100%; } }

.modal__inner {
  display: flex;
  width: 728px;
  min-height: 444px;
  position: fixed;
  top: calc(50% - 295px);
  right: 0;
  left: 0;
  bottom: auto;
  margin: auto;
  text-align: center;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
  z-index: 2;
  background-color: #ffffff;
  padding: 180px 165px; }

.modal {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.8); }

.modal_text {
  font-size: 25px;
  line-height: 43px;
  font-weight: 500; }

.modal__close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 26px;
  height: 28px;
  border: none;
  background-color: inherit;
  cursor: pointer; }

.hidden {
  display: none; }

.about {
  margin-bottom: 185px; }

.about__inner {
  max-width: 990px;
  position: relative; }

.about__header {
  display: flex;
  flex-direction: column; }

.about__title,
.about__text,
.about__timetext,
.about__timedate {
  font-family: MillerMedium;
  margin-top: 0; }

.about__title {
  font-size: 40px;
  line-height: 50px;
  margin-bottom: 10px; }

.about__text {
  font-size: 16px;
  line-height: 24px;
  color: #33B5A3;
  text-transform: uppercase;
  margin-bottom: 97px; }

.about__list {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0; }

.about__itemtitle {
  font-family: MillerMedium;
  margin-top: 0;
  margin-bottom: 12px;
  font-size: 24px;
  line-height: 32px;
  position: relative; }

.about__itemtitle::before {
  content: "";
  position: absolute;
  bottom: 80px;
  left: 0;
  right: 0;
  max-width: 450px;
  border-bottom: 4px solid #63D2C1; }

.about__itemtext {
  margin-top: 0;
  font-size: 16px;
  line-height: 24px;
  color: #666666;
  max-width: 420px; }

.about__item:not(:last-child) {
  margin-bottom: 130px; }

.about__time {
  width: 450px;
  border: 1px solid #33B5A3;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 57%;
  left: 50%;
  text-align: center;
  padding-top: 40px;
  padding-bottom: 40px; }

.about__timetext {
  margin-bottom: 7px;
  font-size: 24px;
  line-height: 32px; }

.about__timedate {
  font-size: 64px;
  line-height: 60px;
  letter-spacing: -0.5px;
  margin-bottom: 0; }

.slider {
  margin-bottom: 138px; }

.slide {
  padding-right: 200px;
  position: relative; }

.slider__inner {
  position: relative; }

.slider__wrapper {
  overflow: hidden; }

.slider__wrapperinner {
  display: flex;
  justify-content: space-around;
  transition: 0.5s all; }

.slider__header {
  max-width: 950px;
  position: relative; }

.slider__button {
  position: absolute;
  width: 20px;
  height: 40px; }

.slider__image {
  width: 750px;
  height: 500px; }

.slider__picture {
  padding-right: 200px; }

.slider__title,
.slider__text {
  margin-top: 0;
  font-family: MullerMedium; }

.slider__title {
  margin-bottom: 8px;
  font-size: 40px;
  line-height: 50px; }

.slider__text {
  margin-bottom: 50px;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  color: #33B5A3; }

.slider__remark {
  position: absolute;
  top: 0;
  left: 93%;
  transform: translateX(-93%);
  width: 136px; }

.slider__advertisement {
  font-size: 13px;
  line-height: 20px;
  margin: 0; }

.slider__button {
  width: 25px;
  height: 45px;
  background-color: inherit;
  border: none;
  cursor: pointer; }

.slider__button--left,
.slider__button--right {
  position: absolute;
  top: 585px;
  z-index: 1; }

.slider__button--left {
  background-image: url(../img/arrowleft.svg);
  background-repeat: no-repeat;
  left: 82%; }

.slider__button--right {
  background-image: url(../img/arrowright.svg);
  background-repeat: no-repeat;
  left: 90%; }

.slider__button--right:hover,
.slider__button--right:focus {
  background-image: url(../img/arrowrighthover.svg); }

.slider__button--left:hover,
.slider__button--left:focus {
  background-image: url(../img/arrowlefthover.svg); }

.form {
  margin-bottom: 135px; }

.form__inner {
  max-width: 985px; }

.form__main {
  display: flex; }

.form__group {
  padding: 54px 130px 59px 130px;
  border: 3px solid #63D2C1; }

.form__inputs {
  display: flex;
  flex-direction: column;
  align-items: center; }

.form__inputs input {
  padding: 18px 277px 18px 25px;
  max-width: 500px;
  font-size: 18px;
  line-height: 24px; }

.form__name,
.form__email {
  margin-bottom: 29px; }

.form__phone {
  margin-bottom: 48px; }

.form__legend {
  text-align: center;
  font-family: MullerMedium;
  font-size: 40px;
  line-height: 50px;
  letter-spacing: -0.2px; }

.form__title {
  margin-top: 0;
  margin-bottom: 57px;
  position: relative;
  font-size: 16px;
  line-height: 24px;
  max-width: 280px; }

.form__title::before {
  content: "";
  position: absolute;
  left: -67px;
  top: -2px;
  background-repeat: no-repeat;
  width: 46px;
  height: 69px;
  background-image: url(../img/dooricon.svg); }

.form__rules {
  display: flex;
  flex-direction: column;
  align-items: center; }

.form__button {
  padding: 17px 76px 15px;
  background-color: #F9572E;
  border-radius: 30px;
  border: none;
  color: #ffffff;
  cursor: pointer;
  margin-bottom: 37px; }

.form__button:hover,
.form__button:focus,
.form__ruleslink:hover,
.form__ruleslink:focus {
  opacity: 0.5; }

.form__rulestest {
  margin-top: 0;
  margin-bottom: 5px; }

.form__ruleslink {
  color: #F9572E; }

.footer {
  background-color: #F2F2F2; }

.footer__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative; }

.footer__social {
  margin-top: 72px;
  margin-bottom: 32px; }

.social__list {
  list-style: none;
  padding-left: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; }

.social__item {
  margin-right: 30px; }

.social__link {
  min-width: 50px;
  height: 50px;
  background-color: #F9572E;
  display: inline-flex;
  border: none;
  color: #ffffff; }

.social__link:hover,
.social__link:focus {
  opacity: 0.5;
  box-shadow: 0 7px 15px #F9572E; }

.social__link svg {
  margin: auto; }

.footer__title,
.footer__text {
  margin-top: 0; }

.footer__title {
  margin-bottom: 5px;
  font-size: 21px;
  line-height: 26px; }

.footer__text {
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 0; }

.footer__header {
  max-width: 440px;
  margin-bottom: 75px;
  display: flex;
  flex-direction: column;
  align-items: center; }

.footer__contacts {
  display: grid;
  grid-template-columns: 250px 250px 250px;
  justify-items: center;
  margin-bottom: 110px;
  grid-column-gap: 115px; }

.footer__address {
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center; }

.footer__link,
.footer__address {
  font-size: 21px;
  line-height: 30px; }

.footer__city {
  color: #666666;
  font-size: 16px;
  line-height: 24px; }

.footer__copyrighttext {
  margin: 0;
  position: relative; }

.footer__inner::before {
  content: "";
  position: absolute;
  bottom: 80px;
  left: 75px;
  right: 75px;
  max-width: 1720px;
  border-bottom: 1px solid #CCCCCC; }

.footer__link-tel,
.footer__link-mail {
  position: relative; }

.footer__link-tel::before {
  content: "";
  position: absolute;
  left: -40px;
  top: -2px;
  background-repeat: no-repeat;
  width: 24px;
  height: 27px;
  background-image: url(../img/phonesvg.svg); }

.footer__link-mail::before {
  content: "";
  position: absolute;
  left: -40px;
  top: 2px;
  background-repeat: no-repeat;
  width: 32px;
  height: 27px;
  background-image: url(../img/mailsvg.svg); }

.footer__link-mail:hover,
.footer__link-mail:focus {
  color: #F9572E; }

.footer__link-mail::after {
  content: "";
  position: absolute;
  bottom: 25px;
  left: 0;
  right: 0;
  max-width: 450px;
  border-bottom: 2px solid #F9572E; }
